<template>
    <div id="app">
        <div class="help"><span>*</span> обязательные поля</div>
        <form>
            <h1>Форма создания Клиента</h1>
            <base-input
                v-model="state.firstName"
                :typeInp="'text'"
                :plcHold="'Фамилия'"
                class="required"
            />
            <base-error
                v-if="v$.firstName.$error"
                :msg="'Это поле должно содержать только буквы'"
            />
            <base-input
                v-model="state.secondName"
                :typeInp="'text'"
                :plcHold="'Имя'"
                class="required"
            />
            <base-error
                v-if="v$.secondName.$error"
                :msg="'Это поле должно содержать только буквы'"
            />
            <base-input
                v-model="state.surName"
                :typeInp="'text'"
                :plcHold="'Отчество'"
            />
            <base-input
                v-model="state.birthDay"
                :typeInp="'date'"
                :plcHold="'Дата рождения'"
                class="required"
            />
            <base-error
                v-if="v$.birthDay.$error"
                :msg="'Дата рождения содержит только цифры в формате мм/дд/гггг'"
            />
            <base-input
                v-model="state.contact.tel"
                :typeInp="'tel'"
                :plcHold="'Номер телефона'"
                maxlength="11"
                class="required"
            />
            <base-error
                v-if="v$.contact.tel.$error"
                :msg="'Телефон должен начинаться с цифры 7 и содержать 11 цифр'"
            />
            <div class="radio-box">
                Пол:
                <label class="radio" v-for="item in data.sexData" :key="item">
                    <input
                        v-model="state.sex"
                        name="sex"
                        :value="item"
                        type="radio"
                    />
                    {{ item }}
                </label>
            </div>
            <multi-select
                class="required"
                v-model="state.groupClients"
                :options="[
                    { name: 'VIP' },
                    { name: 'Проблемные' },
                    { name: 'ОМС' },
                ]"
                nameLabel="groupClients"
                :title="'Группа клиентов:'"
                placeHol="Выберите группу клиентов"
                bindLabel="name"
                bindValue="name"
                @onVSelect="setResult($event.target.value)"
            />
            <base-error
                v-if="v$.groupClients.$error"
                :msg="'Выберите из предложженых групп одну или более'"
            />
            <base-select
                :name="'therapist'"
                :title="'Лечащий врач:'"
                v-model="state.therapist"
                :options="data.therapist"
                :isMultip="false"
            />
            <label class="label">
                Отправлять СМС?
                <input v-model="state.isSendSms" type="checkbox" />
            </label>
            <h4>Адрес</h4>
            <base-input
                v-model="state.adress.index"
                :typeInp="'text'"
                :plcHold="'Индекс'"
            />
            <base-input
                v-model="state.adress.country"
                :typeInp="'text'"
                :plcHold="'Страна'"
            />
            <base-input
                v-model="state.adress.state"
                :typeInp="'text'"
                :plcHold="'Область'"
            />
            <base-input
                v-model="state.adress.city"
                :typeInp="'text'"
                :plcHold="'Город'"
                class="required"
            />
            <base-error
                v-if="v$.adress.city.$error"
                :msg="'Это поле должно содержать только буквы'"
            />
            <base-input
                v-model="state.adress.street"
                :typeInp="'text'"
                :plcHold="'Улица'"
            />
            <base-input
                v-model="state.adress.house"
                :typeInp="'text'"
                :plcHold="'Дом'"
            />
            <h4>Документ подтверждающий личность</h4>
            <base-select
                :name="'passport'"
                :title="'Тип докумета:'"
                v-model="state.passport.type"
                :options="data.passportType"
                :isMultip="false"
                class="required"
            />
            <base-error
                v-if="v$.passport.type.$error"
                :msg="'Выберите один из предложенных документов'"
            />
            <base-input
                v-model="state.passport.serias"
                :typeInp="'text'"
                :plcHold="'Серия'"
            />
            <base-input
                v-model="state.passport.number"
                :typeInp="'text'"
                :plcHold="'Номер'"
            />
            <base-input
                v-model="state.passport.issue"
                :typeInp="'text'"
                :plcHold="'Выдан'"
            />
            <base-input
                v-model="state.passport.date"
                :typeInp="'date'"
                :plcHold="'Дата выдачи'"
                class="required"
            />
            <base-error
                v-if="v$.passport.date.$error"
                :msg="'Дата выдачи содержит только цифры в формате мм/дд/гггг'"
            />
            <input id="submit" class="invisible" type="submit" />
        </form>
        <div class="btn-box">
            <button class="btn" @click="submitForm">Submit</button>
        </div>
    </div>
</template>

<script>
import BaseInput from "./components/BaseInput.vue";
import BaseSelect from "./components/BaseSelect.vue";
import BaseError from "./components/BaseError.vue";
import { useVuelidate } from "@vuelidate/core";
import { required, numeric, maxLength, minLength } from "@vuelidate/validators";
import { computed, reactive, ref } from "vue";
import MultiSelect from "./components/MultiSelect.vue";

const isAlpha = (value) => {
    if (/^[\p{L}]+$/u.test(value)) return true;
};
const result = ref([]);
const setResult = (val) => {
    result.value = [...val];
};
const data = {
    sexData: ["м", "ж"],
    therapist: ["Иванов", "Захаров", "Чернышева"],
    passportType: ["Паспорт", "Свидетельство о рождении", "Вод. удостоверение"],
};
export default {
    name: "App",
    components: {
        BaseInput,
        BaseSelect,
        BaseError,
        MultiSelect,
    },
    setup() {
        const state = reactive({
            firstName: "",
            secondName: "",
            surName: "",
            birthDay: "",
            contact: {
                tel: "",
            },
            sex: "",
            groupClients: [],
            therapist: "",
            isSendSms: false,
            adress: {
                index: "",
                country: "",
                state: "",
                city: "",
                street: "",
                house: "",
            },
            passport: {
                type: "",
                serias: "",
                number: "",
                issue: "",
                date: "",
            },
        });

        const rules = computed(() => {
            return {
                firstName: { required, isAlpha },
                secondName: { required, isAlpha },
                surName: {},
                birthDay: { required },
                contact: {
                    tel: {
                        required,
                        maxLength: maxLength(11),
                        minLength: minLength(11),
                        numeric,
                        isPhone(tel) {
                            if (tel[0] === "7" || tel[0] === 7) {
                                return true;
                            }
                        },
                    },
                },
                sex: {},
                groupClients: { required },
                therapist: {},
                isSendSms: {},
                adress: {
                    index: {},
                    country: {},
                    state: {},
                    city: { required, isAlpha },
                    street: {},
                    house: {},
                },
                passport: {
                    type: { required },
                    serias: {},
                    number: {},
                    issue: {},
                    date: { required },
                },
            };
        });
        const v$ = useVuelidate(rules, state);
        return {
            data,
            setResult,
            state,
            v$,
        };
    },
    methods: {
        async submitForm() {
            const isValid = await this.v$.$validate();
            console.log(this.data);
            if (isValid) {
                document.querySelector("#submit").click();
                alert(
                    `Новый клиент ${this.state.firstName} ${this.state.secondName} ${this.state.surName} успешно создан`
                );
            } else {
                window.scrollTo({
                    top: 0,
                    behavior: "smooth",
                });
            }
        },
    },
};
</script>

