<template>
    <div class="select">
        <label :for="name" class="select__label"
            >{{ title }}
            <select
                :value="value"
                @change="$emit('input', $event.target.value)"
                :name="name"
                class="select__list"
                :multiple="isMultip"
            >
                <option
                    class="select__item"
                    v-for="option in options"
                    :value="option"
                    :key="option"
                >
                    {{ option }}
                </option>
            </select>
        </label>
        <label v-if="isMultip" class="select__help"
            >*Для выбора нескольких элементов зажмите ctrl(command) и нажмите
            левой кнопкой мыши по нужному элементу</label
        >
    </div>
</template>

<script>
const updateSelect = ( presval, val) => {
    presval = [...val]
}

export default {
    name: "base-select",
    props: ["value"],
    props: {
        title: String,
        name: String,
        isMultip: Boolean,
        options: [],
    }, 
};
</script>

<style lang="scss" scoped>
.select {
    position: relative;
    border: none;
    width: 100%;
    min-height: 30px;
    padding: 5px 15px;
    margin-bottom: 10px;
    &__label {
        vertical-align: top;
        display: block;
        margin-bottom: 6px;
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
        padding: 5px 15px;
        letter-spacing: 0.04em;
        color: #686ea1;
    }
    &__list {
        width: 100%;
        min-height: 30px;
        overflow: hidden;
        border: 1px solid rgba(#686ea1, 0.1);
        padding: 15px;
    }
    &__item {
        padding: 5px;
    }
    &__help {
        color: rgba(#686ea1, 0.7);
        font-size: clamp(12px, 2vw, 14px);
        background: transparent;
    }
}
@media only screen and (max-width: 590px) {
    .select__help {
        display: none;
    }
}
</style>
