<template>
    <div class="select__wrapper" ref="selectWrapperRef">
        <label :for="nameLabel" class="select__label"
            >{{ title }}
            <div class="base__dropdown-select" @click="showDropdown">
                <div class="base__dropdown-select-item"></div>
                <select :class="styleSelectClass" ref="multiselectRef">
                    <option value="">{{ message }}</option>
                </select>
            </div>
            <div
                :class="styleDropdownClass"
                @change="$emit('input', selected)"
                v-show="show"
                ref="dropdownRef"
            >
                <div v-if="hasSearch" :class="styleSearchClass">
                    <label
                        >Search (min 3):
                        <span>
                            <input type="text" v-model="filter" />
                            <button
                                @click="filter = ''"
                                :disabled="filter.length < 1"
                            >
                                Clear
                            </button>
                        </span>
                    </label>
                </div>
                <ul>
                    <li v-show="filter.length < 3">
                        <label>
                            <input
                                type="checkbox"
                                ref="toggleAllIdRef"
                                @change="toogleAll($event.target.checked)"
                            />
                            <span></span>
                            <strong>Select All</strong></label
                        >
                    </li>
                    <li
                        v-for="(option, index) in options"
                        :key="`fsjh${index}ruei`"
                    >
                        <label>
                            <input
                                type="checkbox"
                                :value="option[bindValue]"
                                v-model="selected"
                            />
                            <span></span>
                            {{ option[bindLabel] }}</label
                        >
                    </li>
                </ul>
            </div>
        </label>
    </div>
</template>

<script>
import { ref, computed, watch } from "vue";

export default {
    name: "multi-select",
    props: {
        options: {
            type: Array,
            required: true,
        },
        placeHol: {
            type: String,
            required: false,
        },
        bindLabel: {
            type: String,
            required: false,
        },
        bindValue: {
            type: String,
            required: false,
        },
        preSelected: {
            type: Array,
            required: false,
        },
        styleSelectClass: {
            type: String,
            required: false,
        },
        styleDropdownClass: {
            type: String,
            required: false,
        },
        styleSearchClass: {
            type: String,
            required: false,
        },
        hasSearch: {
            type: Boolean,
            required: false,
        },
        title: {
            type: String,
            required: false,
        },
        nameLabel: {
            type: String,
            required: true,
        },
    },
    setup(props, { emit }) {
        const propsOptions = computed(() => props.options);
        const options = ref(propsOptions.value || []);
        const show = ref(false);
        const message = computed(() => {
            const len = selected.value.length;
            const optlen = propsOptions.value.length;
            if (len == 0) {
                return props.placeHol;
            } else if (len == 1) {
                const r = propsOptions.value.filter(
                    (v) => v[bindValue.value] == selected.value[0]
                );
                if (r.length > 0) {
                    return r[0][bindLabel.value];
                }
            } else if (len <= optlen) {
                return selected.value.join(", ");
            }
        });
        const multiselectRef = ref(null);
        const selectWrapperRef = ref(null);
        const dropdownRef = ref(null);
        const toggleAllIdRef = ref(null);

        const bindLabel = ref(props.bindLabel || "label");
        const bindValue = ref(props.bindValue || "value");
        const selected = ref(props.preSelected ? [...props.preSelected] : []);

        const styleSelectClass = ref(
            props.styleSelectClass || "base__select-class"
        );
        const styleDropdownClass = ref(
            props.styleDropdownClass || "base__dropdown-class"
        );
        const styleSearchClass = ref(
            props.styleSearchClass || "base__search-select"
        );
        const hasSearch = ref(props.hasSearch || false);
        const filter = ref("");

        const showDropdown = () => {
            const check = show.value;
            show.value = true;
            if (!check) {
                setTimeout(() => {
                    window.addEventListener("click", useDetectOutsideClick);
                }, 50);
            }
        };

        const hideDropdown = () => {
            show.value = false;
        };

        const toogleAll = (toggle) => {
            selected.value = [];
            if (toggle) {
                options.value.forEach((v) => {
                    selected.value.push(v[bindValue.value]);
                });
            }
        };

        const useDetectOutsideClick = (event) => {
            if (
                event.target !== dropdownRef.value &&
                event.composedPath().includes(dropdownRef.value)
            ) {
                return;
            }
            hideDropdown();
            window.removeEventListener("click", useDetectOutsideClick);
        };

        watch(
            () => propsOptions.value,
            (value) => {
                options.value = [...value];
            },
            { deep: true }
        );
        watch(
            () => selected.value,
            (value) => {
                emit("onVSelect", value);
                if (selected.value.length == propsOptions.value.length) {
                    toggleAllIdRef.value.checked = true;
                } else {
                    toggleAllIdRef.value.checked = false;
                }
            },
            { deep: true }
        );
        watch(
            () => filter.value,
            (value) => {
                if (value.length >= 3) {
                    options.value = propsOptions.value.filter((v) =>
                        v[bindLabel.value]
                            .toLowerCase()
                            .includes(filter.value.toLowerCase())
                    );
                } else {
                    options.value = [...propsOptions.value];
                }
            }
        );
        return {
            options,
            show,
            multiselectRef,
            selectWrapperRef,
            dropdownRef,
            toggleAllIdRef,
            bindLabel,
            bindValue,
            showDropdown,
            selected,
            styleSelectClass,
            styleDropdownClass,
            styleSearchClass,
            hasSearch,
            filter,
            message,
            toogleAll,
        };
    },
};
</script>

<style lang="scss" scoped>
.select__wrapper {
    color: #2c3e50;
    width: 100%;
}
input {
    color: #2c3e50;
}
.select__label {
    vertical-align: top;
    display: block;
    margin-bottom: 6px;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    padding: 5px 15px;
    letter-spacing: 0.04em;
    color: #686ea1;
}
.base__dropdown-select {
    position: relative;
    cursor: pointer;
    width: 100%;
    display: inline-flex;
}
.base__dropdown-select-item {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}
.base__select-class {
    width: 100%;
    min-height: 30px;
    overflow: hidden;
    border: 1px solid rgba(#686ea1, 0.2);
    padding: 15px;
    color: #494e80;
}
.base__dropdown-class {
    position: relative;
    background-color: white;
}

.base__dropdown-class ul {
    z-index: 1;
    border: 1px solid rgba(#686ea1, 0.2);
    border-top: 0;
    border-radius: 0 0 3px 3px;
    left: 0px;
    padding: 8px 8px;
    position: absolute;
    top: -1rem;
    width: 100%;
    list-style: none;
    max-height: 150px;
    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    box-sizing: border-box;
    background-color: white;
}

.base__dropdown-class ul li {
    padding: 5px 0 4px;
    width: 100%;
    display: flex;
    align-items: center;
}

.base__dropdown-class ul li label {
    cursor: pointer;
}

.base__dropdown-class ul li:not(:last-child) {
    border-bottom: 1px solid rgba(#686ea1, 0.2);
}

.base__dropdown-class ul li input {
    margin-right: 8px;
    cursor: pointer;
    position: absolute;
    opacity: 0;
    transition: all 0.3s;
}

.base__dropdown-class ul li input + span {
    position: relative;
    top: 0px;
    background: white;
    content: "";
    display: inline-block;
    margin: 0 0.5em 0 0;
    padding: 0;
    vertical-align: middle;
    width: 18px;
    height: 18px;
    min-width: 18px;
    border: 1px solid rgba(#686ea1, 0.4);
    transform: translate3d(0, 0, 0);
    backface-visibility: hidden;
}

.base__dropdown-class ul li input + span:after {
    background-size: 70%;
    position: absolute;
    background-repeat: no-repeat;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    content: " ";
    background-position: center;
    color: green;
}

.base__dropdown-class ul li input:checked + span::after {
    /*transform: translate(-50%, -50%) scale(1) !important;*/
    background-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgdmlld0JveD0iMCAwIDI0IDI0IiBmaWxsPSJncmVlbiI+PHBhdGggZD0iTTIwLjI4NSAybC0xMS4yODUgMTEuNTY3LTUuMjg2LTUuMDExLTMuNzE0IDMuNzE2IDkgOC43MjggMTUtMTUuMjg1eiIvPjwvc3ZnPg==");
}

.base__dropdown-class ul li input + span {
    border-radius: 2px;
    transition: all 0.3s;
}

.base__dropdown-class ul li label {
    line-height: 1.2;
    display: flex;
    align-items: center;
    text-align: start;
}
.base__dropdown-class .base__search-select {
    background-color: white;
    flex: 1;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
    padding: 8px 8px;
    box-sizing: border-box;
    border-left: 1px solid rgba(#686ea1, 0.2);
    border-right: 1px solid rgba(#686ea1, 0.2);
    text-align: start;
    height: 60px;
}

.base__dropdown-class .base__search-select label {
    display: flex;
    flex-direction: column;
    line-height: 1;
    font-size: 12px;
    width: 100%;
}

.base__dropdown-class .base__search-select label > span {
    display: flex;
    gap: 4px;
}

.base__dropdown-class .base__search-select label > span button {
    white-space: nowrap;
    cursor: pointer;
}

.base__dropdown-class .base__search-select label input[type="text"] {
    font-size: 1rem;
    padding: 0 10px;
    line-height: 1.5;
    width: 100%;
    height: 30px;
}

.base__dropdown-class .base__search-select + ul {
    top: 60px;
    padding-top: 0px;
    margin-top: 0;
}
</style>
